.popModalBackground{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
  opacity: 1;
  pointer-events: none;
  overflow: hidden;
}

.popModalBackground.show {
  opacity: 1;
  pointer-events: auto;
  overflow: hidden;
}

.PopProductCross{
  display: flex;
  justify-content: flex-end;
  align-items: flex-start; /* add this line */
}

.PopProductCross button {
  background-color: transparent;
  border: none;
  font-size: 25px;
  cursor: pointer;
  margin-left: 10px;
}

.makeitgray{
  color: gray;
}

/* New code------------------------------------------------------------------ */
.modal {
  width: 300px;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  align-items: center;
  justify-content: center;
  display: grid;
}

.modal h2 {
  margin-top: 0;
}

.modal img {
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.modal p {
  margin: 0;
}

.add-to-cart {
  background-color: white;
  color: black;
  padding: 10px 20px;
  border: 2px solid black;
  border-color: black;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s;
  font-size: 16px;
}

.add-to-cart:hover:not(:disabled) {
  background-color: black;
  color: white;
}

.image-wrapper {
  max-width: 200px;
  max-height: 200px;
  overflow: hidden;
}

.image-wrapper img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}

.add-to-cart-container{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
}
